.form__checkbox-label {
  letter-spacing: .03em;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  align-self: center;
  margin-top: 20px;
  margin-bottom: 30px;
  padding-left: 30px;
  font-size: 14px;
  font-weight: 400;
  line-height: 24px;
  position: relative;
