  width: 15px;
  height: 15px;
  fill: rgba(0, 0, 0, 0);
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: -1px;
  left: -1px;
}

.modal__close {
  width: 30px;
  height: 30px;
  cursor: pointer;
  box-sizing: border-box;
  background: #fff;
  border: 1px solid rgba(0, 0, 0, .1);
  border-radius: 50%;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 8px;
  right: 8px;
}

.modal__close:before, .modal__close:after {
  content: "";
  width: 15px;
  height: 1.5px;
  background-color: #000;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 50%;
  left: 50%;
}

.modal__close:after {
  transform: translate(-50%, -50%)rotate(45deg);
}

.modal__close:before {
  transform: translate(-50%, -50%)rotate(-45deg);
}

.modal__close:hover:after, .modal__close:hover:before {
  background-color: #2196f3;
}

